import { CompareResult } from './types.js';
import { clean, compareVersion, validateWithError } from './utils.js';

/**
 * @description
 * Determines if two versions are equal.
 *
 * @param verA - The first version.
 * @param verB - The second version.
 * @returns True if the versions are equal, false otherwise.
 *
 * @docsCategory preference/utils/semver
 * @docsPage semver-eq
 * @codeFilter eq
 */
export const eq = (verA: string, verB: string): boolean => {
  if (validateWithError(verA, verB)) {
    return compareVersion(clean(verA), clean(verB)) === CompareResult.Equal;
  }
  return false;
};
